import { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import styles from "./MyProfile.module.css";
import Loader from "../components/Loader";

export default function MyProfile() {
  const [stats, setStats] = useState(null);
  const [statsLoading, setStatsloading] = useState(true);
  const { user, loading, isAuthenticated } = useContext(AuthContext);
  const userId = user?._id;
  const navigate = useNavigate();

  //    USECONTEXT
  useEffect(() => {
    if (loading) return;
    if (!isAuthenticated) {
      navigate("/login");
    }
  }, [isAuthenticated, navigate]);

  //           
  useEffect(() => {
    if (loading) return;

    let isMounted = true;

    async function fetchStats() {
      setStatsloading(true);
      try {
        const res = await fetch(`/api/post/${userId}/stats`, {
          credentials: "include",
        });

        const data = await res.json();

        if (!res.ok) throw new Error(data.message || "Error fetching stats");

        if (isMounted) {
          setStats(data);
        }
      } catch (err) {
        console.error("Error fetching stats:", err);
        if (isMounted) setStatsloading(false);
      } finally {
        if (isMounted) {
          setStatsloading(false);
        }
      }
    }
    fetchStats();
  }, [loading]);

  if (loading || statsLoading) {
    return <Loader text=".." />;
  }

  return (
    <div className={styles.middle}>
      <div className={styles.statsFloating}>
        <h1>{stats.nameOfUser} </h1>
        <h2>  </h2>

        <p className={styles.statsDescription}>
                    . 
                   .
        </p>

        <div className={styles.statsList}>
          <p>  : {stats.clickView}</p>
          <p>   : {stats.ticketsSold}</p>
          <p>   : {stats.totalRevenue}</p>
          <p>  : {stats.totalCommission.toFixed(2)}</p>
        </div>
      </div>
    </div>
  );
}
