import { useState } from "react";
import Message from "../components/message";
import styles from "./myCardItem.module.css";
import EditPostForm from "./editFormData";
import { Link } from "react-router-dom";

export default function MyCardItem(props) {
  const [message, setMessage] = useState("");
  const [edit, setEdit] = useState(false);
  const [postData, setPostData] = useState(props);
  const { id, onDelete } = props;

  async function handleDelete() {
    try {
      const response = await fetch(`/api/post/${id}`, {
        method: "DELETE",
        credentials: "include",
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || "Failed to delete post");
      }

      setMessage("  ");
      onDelete();
    } catch (error) {
      setMessage(" ");
      console.error("Error deleting post:", error);
    }
  }

  return (
    <div className={styles.card}>
      {edit ? (
        <EditPostForm
          id={id}
          post={postData}
          onUpdate={(newData) => {
            setPostData(newData);
            setEdit(false);
          }}
          onCancel={() => setEdit(false)}
        />
      ) : (
        <>
          <Link to={`/card-details/${id}`}>
            <img
              src={postData.imageUrl}
              alt={postData.title}
              className={styles.image}
            />
          </Link>
          <div className={styles.content}>
            <h2>{postData.title}</h2>
            <p>
              <strong>:</strong> {postData.location}
            </p>
            <p>
              <strong> :</strong> {postData.price}
            </p>
            <p>
              <strong>: </strong>
              {new Date(postData.date).toLocaleDateString()}
            </p>
            <p>
              <strong>:</strong> {postData.body}
            </p>
            <button onClick={handleDelete} className={styles.button}>
              
            </button>
            <button onClick={() => setEdit(true)} className={styles.button}>
              
            </button>
          </div>
          <Message
            message={message}
            setMessage={setMessage}
            className={styles.message}
          />
        </>
      )}
    </div>
  );
}
