import styles from "./createPartyCard.module.css";
import { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import { Tickets, User, CreditCard, Menu, X } from "lucide-react";
import { AuthContext } from "../context/AuthContext";

export default function CreatePartyCardPage() {
  const { isAuthenticated, loading } = useContext(AuthContext);
  const [title, setTitle] = useState("");
  const [location, setLocation] = useState("");
  const [date, setDate] = useState("");
  const [price, setPrice] = useState("");
  const [body, setBody] = useState("");
  const [imageUrl, setImageUrl] = useState("");
  const [loadingCard, setLoadingCard] = useState(false);
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();

  //    USECONTEXT
  useEffect(() => {
    if (loading) return;
    if (!isAuthenticated) {
      navigate("/login");
    }
  }, [isAuthenticated, navigate]);

  async function handleSubmit(e) {
    e.preventDefault(); //         
    setLoadingCard(true);
    try {
      const response = await fetch("/api/post/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include", //    
        body: JSON.stringify({ title, location, price, date, body, imageUrl }),
      });

      const data = await response.json(); //   

      if (!response.ok) {
        throw new Error(data.message || "upload failed");
      }

      console.log("upload successful:", data);
      alert("  !");
      setTitle("");
      setLocation("");
      setDate("");
      setPrice("");
      setBody("");
      setImageUrl("");
    } catch (error) {
      console.error("Error during logined:", error);
      alert(error.message);
    } finally {
      setLoadingCard(false);
    }
  }

  return (
    <div className={styles.middle}>
      <h1>   </h1>
      <div className={styles.container}>
        <div className={styles.header}>
          <h2>   </h2>
          <p>
              :   ,   ,  
                
          </p>
        </div>

        <form className={styles.form} onSubmit={handleSubmit}>
          <input
            type="text"
            placeholder=" "
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
          />

          <input
            type="text"
            value={location}
            onChange={(e) => setLocation(e.target.value)}
            placeholder=" "
            required
          />
          <input
            type="text"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
            placeholder=" "
            required
          />
          <input
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
          <textarea
            className={styles.textarea}
            placeholder="    (   )"
            minLength={3}
            maxLength={200}
            value={body}
            onChange={(e) => setBody(e.target.value)}
            required
          />
          <input
            type="text"
            placeholder=" "
            value={imageUrl}
            onChange={(e) => setImageUrl(e.target.value)}
          />
          <button type="submit">{loadingCard ? ".." : " "}</button>
        </form>
      </div>

      <div className={styles.menu}>
        <div className={`${styles.toggle} ${open ? styles.open : ""}`}>
          <button>
            <Link className={styles.signup} to="/all-cards">
              <Tickets size={20} />
              {/* <span> </span> */}
            </Link>
          </button>
          <button>
            <Link className={styles.signup} to="/my-cards">
              <User size={20} />
              {/* <span> </span> */}
            </Link>
          </button>
          <button>
            <Link className={styles.signup} to="/payment">
              <CreditCard size={20} />
              {/* <span> </span> */}
            </Link>
          </button>
        </div>

        <button
          className={`${styles.mainbutton} ${open ? styles.active : ""}`}
          onClick={() => setOpen(!open)}
        >
          {open ? <X size={28} /> : <Menu size={28} />}
        </button>
      </div>
    </div>
  );
}
