import { useEffect, useState } from "react";
import styles from "./paymentPage.module.css";
import { useNavigate } from "react-router-dom";

export default function PaymentPage() {
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");
  const [haspaid, setHaspaid] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    cardNumber: "",
    expiry: "",
    cvv: "",
  });
  const navigate = useNavigate();
  const initialForm = { name: "", cardNumber: "", expiry: "", cvv: "" };

  //   ,    
  useEffect(() => {
    if (message) {
      const timer = setTimeout(() => {
        setLoading(false);
        setMessage("");
      }, 1000);

      //       
      return () => clearTimeout(timer);
    }
  }, [message]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  //  
  useEffect(() => {
    const checkAuth = async () => {
      try {
        const res = await fetch("/api/auth/check-auth", {
          credentials: "include", //      -cookie
        });

        if (!res.ok) {
          throw new Error("Unauthorized");
        }

        const data = await res.json();
        console.log("data:", data.user);

        if (data.user.hasPaid) {
          setHaspaid(true);
        }

        console.log(" :", data);
      } catch (err) {
        console.log("    ");
        navigate("/login");
      }
    };

    checkAuth();
  }, [navigate]);

  const handlePayment = async () => {
    setLoading(true);

    try {
      const res = await fetch("/api/payment/topay", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
        credentials: "include", //      
      });

      const data = await res.json();

      if (!res.ok) {
        setMessage(data.message);
        setFormData(initialForm);
        return;
      }

      if (data.user.hasPaid) {
        setHaspaid(true);
      }

      setMessage(data.message);
      setFormData(initialForm);
    } catch (err) {
      setMessage(" ");
    }
  };

  const handleDeletePayment = async () => {
    setLoading(true);
    try {
      const res = await fetch("/api/payment/deletepay", {
        method: "DELETE",
        credentials: "include", //      
      });

      const data = await res.json();

      setHaspaid(false);
      setMessage(data.message);
    } catch (err) {
      setMessage(" ");
    }
  };

  return (
    <div className={styles.paymentContainer}>
      <div className={styles.paymentBox}>
        <h1>  </h1>
        <p>      </p>

        <input
          type="text"
          name="name"
          placeholder="  "
          value={formData.name}
          onChange={handleChange}
          className={styles.inputField}
        />

        <input
          type="text"
          name="cardNumber"
          placeholder=" "
          value={formData.cardNumber}
          onChange={handleChange}
          className={styles.inputField}
        />

        <div className={styles.row}>
          <input
            type="text"
            name="expiry"
            placeholder=" (MM/YY)"
            value={formData.expiry}
            onChange={handleChange}
            className={`${styles.inputField} ${styles.halfInput}`}
          />
          <input
            type="text"
            name="cvv"
            placeholder="CVV"
            value={formData.cvv}
            onChange={handleChange}
            className={`${styles.inputField} ${styles.halfInput}`}
          />
        </div>

        <button
          onClick={handlePayment}
          disabled={loading}
          className={styles.payBtn}
        >
          {loading ? " ..." : " "}
        </button>

        {haspaid && (
          <button onClick={handleDeletePayment} className={styles.payBtn}>
             
          </button>
        )}

        {message && <p className={styles.successMsg}>{message}</p>}
      </div>
    </div>
  );
}
