import { useState } from "react";
import Loader from "./Loader";
import styles from "./editFormData.module.css";

export default function EditPostForm({ id, post, onUpdate, onCancel }) {
  const [title, setTitle] = useState(post.title);
  const [location, setLocation] = useState(post.location);
  const [price, setPrice] = useState(post.price);
  const [date, setDate] = useState(post.date.slice(0, 10));
  const [body, setBody] = useState(post.body);
  const [imageUrl, setImageUrl] = useState(post.imageUrl);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      const response = await fetch(`/api/post/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({ title, location, price, date, body, imageUrl }),
      });

      //  
      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || "Update failed");
      }

      if (
        data.title === post.title &&
        data.location === post.location &&
        data.price === post.price &&
        data.date === post.date &&
        data.body === post.body &&
        data.imageUrl === post.imageUrl
      ) {
        alert("  ");
        return;
      } else {
        alert("  !");
        onUpdate(data); //     
      }
    } catch (err) {
      console.error(" :", err);
      alert(err.message);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <Loader text="..." />;
  }

  return (
    <form className={styles.form} onSubmit={handleSubmit}>
      <h3> </h3>

      <input
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        placeholder=""
        required
      />
      <input
        value={location}
        onChange={(e) => setLocation(e.target.value)}
        placeholder=""
        required
      />
      <input
        type="text"
        value={price}
        onChange={(e) => setPrice(e.target.value)}
        placeholder=" "
        required
      />
      <input
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
        required
      />
      <textarea
        value={body}
        onChange={(e) => setBody(e.target.value)}
        placeholder=""
        required
      />
      <input
        value={imageUrl}
        onChange={(e) => setImageUrl(e.target.value)}
        placeholder=" "
        required
      />

      <button type="submit"></button>
      <button type="button" onClick={onCancel}>
        
      </button>
    </form>
  );
}
